import Vue from "vue";
import Vuex from "vuex";
import mutations from "./mutations.js";
import actions from "./actions.js";

Vue.use(Vuex);

const store = new Vuex.Store({
    state:{
        topiclist:[    //
            {name:"all",desc:""},
            {name:"good",desc:""},
            {name:"share",desc:""},
            {name:"ask",desc:""},
            {name:"jod",desc:""},
            {name:"dev",desc:""}
          ],
           isShow:false,   //actions
           islogined:false,  //
           loginedMenus:{
           create:"",
           center:"",
           msgs:"",
           logout:""
          } ,
          unloginMenus:{
          login:""
          },
          loginname:"", //
          author_id:"", 
          accesstoken:"",  //
          msgsCount:"" 
    },
    getters:{
        //
        mormenus(state){
            if(state.islogined){    //
              return state.loginedMenus  //
            }
            return state.unloginMenus      //
              },
        //
        fileterTab(state){
           return function(tab){
             for (let i = 0; i < state.topiclist.length; i++) {
               const element = state.topiclist[i];
               if (element.name == tab) {
                 return state.topiclist[i].desc;
               }
             }
           }
        }
    },
    mutations:mutations,
    actions:actions,
})
export default store;


